<template>
    <div id="TittleNameBox" ref="tittleBox">
        <h1>{{ rol }}</h1>
    </div>
</template>

<script>
import { ref, onMounted } from 'vue';

export default {
    name: 'TittleRol',
    props:{
        rol: { type: String, required: true },
    }
    ,
    setup() {
        const angle = ref(0); // Define el ángulo como referencia reactiva
        const tittleBox = ref(null); // Referencia al elemento del DOM

        const rotateBackground = () => {
            const rotate = () => {
                angle.value += 1; // Incrementa el ángulo de rotación
                if (angle.value >= 360) angle.value = 0; // Reinicia el ángulo si llega a 360
                if (tittleBox.value) {
                    tittleBox.value.style.background = `linear-gradient(${angle.value}deg, #CA1551, #07001b00, #07001b5e, #07001b00, #CA1551)`;
                }
                requestAnimationFrame(rotate); // Llama de nuevo a rotate en el siguiente cuadro
            };
            requestAnimationFrame(rotate);
        };

        onMounted(() => {
            rotateBackground();
        });

        return { tittleBox };
    }
};
</script>

<style scoped>
#TittleNameBox {
    border-radius: 25% 75% 75% 25% / 50% 91% 9% 50%  ;
    align-items: center;
    justify-content: center;
    display: flex;
    background: linear-gradient(0deg, #CA1551, #07001b00, #07001b5e, #07001b00, #CA1551);
    filter: hue-rotate(0deg);
    animation: hue-rotate 10s infinite;
    box-shadow: 0px 0px 10px #ca155142;
    width: 100%;
    height: 60%;
    color: #ff79a6;
    font-family: 'MetalLord';
    text-shadow: 0px 0px 20px #CA1551,0px 0px 10px #CA1551 ;
}
h1{
    font-size: 3dvw;
    text-align: center;
    margin-top: 20%;
}

@keyframes hue-rotate {
    0%{
        filter: hue-rotate(0deg); 
    }
    33%{
        filter: hue-rotate(0deg);
    }
    37%{
        filter: hue-rotate(250deg);
    }
    50%{
        filter: hue-rotate(250deg);
    }
    68%{
        filter: hue-rotate(250deg);
    }
    72%{
        filter: hue-rotate(360deg);
    }
    100%{
        filter: hue-rotate(360deg);
    }
}
</style>