<template>
    <nav class="navbar navbar-expand-lg custom-navbar">
      <div class="container-fluid master-box-nav">
        <a class="navbar-brand" href="#">
          <img class="img-logo" src="../assets/imagenes/Logo-128p.png" alt="Logo">
        </a>
  
        <!-- Switch de idioma -->
        <div class="language-switch">
        <input type="checkbox" id="lenguaje-op" @change="toggleLanguage">
          <label for="lenguaje-op" class="switch-label">
            <span class="flag flag-spanish">ES</span>
            <span class="flag flag-english">EN</span>
          </label>
        </div>
  
        <button
          class="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
  
        <div class="collapse navbar-collapse justify-content-center" id="navbarSupportedContent">
          <ul class="navbar-nav mb-2 mb-lg-0">
            <li class="nav-item">
              <a class="nav-link" aria-current="page" href="#">{{ Home }}</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#">{{ AboutMe }}</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#">{{ MyProjects }}</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#">{{ MySkills }}</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#">{{ Contacts }}</a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </template>
  
  <script>
  export default {
    name: 'NavBar',
    props: {
      Home: { type: String, required: true },
      AboutMe: { type: String, required: true },
      MyProjects: { type: String, required: true },
      MySkills: { type: String, required: true },
      Contacts: { type: String, required: true }
    },
    methods: {
      toggleLanguage(event) {
        // Emitir un evento al componente padre para cambiar el idioma
        this.$emit('language-changed', event.target.checked);
      },
    }
  }
  </script>
  
  <style scoped>
  nav{
    animation: entrance 1s 1;
  }

  @keyframes entrance {
    0%{
      transform: translateX(-105%);
    }
    100%{
      transform: translateX(0%);
    }
  }

  .custom-navbar {
    background: #130e22;
    box-shadow: 0px 3px 60px #CA1551, 0px 3px 1px #ff3679;
    border-bottom-right-radius: 100px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
  }
  
  .navbar-brand {
      margin-right: auto; /* El logo se queda en el extremo izquierdo */
  }
  
  a.nav-link {
      color: #1789FC;
      font-family: 'MetalLord';
      font-size: 150%;
      letter-spacing: 1px;
      transition: 0.2s;
      animation: entrance 2s 1;
  }
  
  a.nav-link:hover {
      color: #ff9900;
      text-shadow: 0px 0px 4px #E89005;
      transform: scale(1.1);
  }
  
  .img-logo {
      width: 70%;
      margin: -5%;
      animation: entrance 1.5s 1;
  }
  
  .navbar-toggler {
      margin-right: 6%;
      border-radius: 100px;
  }
  
  .navbar-collapse {
      transition: 1s;
  }
  
  .navbar-nav {
      display: flex;
      justify-content: center; /* Centrar elementos de navegación */
      align-items: center;
  }
  
  /* Switch de idioma */
  .language-switch {
      position: relative;
      display: inline-block;
      width: 60px;
      height: 34px;
      margin-right: 10px;
      animation: entrance 1.7s 1;
  }
  
  .language-switch input {
      opacity: 0;
      width: 0;
      height: 0;
  }
  
  .switch-label {
      position: absolute;
      cursor: pointer;
      width: 100%;
      height: 100%;
      background-color: #14220e;
      border-radius: 34px;
      transition: 0.4s;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 10px;
      margin-top: -40%;
      box-shadow: 0px 0px 30px #51FF00,0px 0px 2px #d8fcc7;
      color: #51FF00;
      font-family: 'MetalLord';
  }
  
  .flag {
      font-size: 20px;
      transition: opacity 0.4s;
  }
  
  .flag-english {
      opacity: 0;
  }
  
  input:checked + .switch-label {
    background-color: #14220e;
    box-shadow: 0px 0px 30px #51FF00,0px 0px 2px #d8fcc7;
    color: #51FF00;
  }
  
  input:checked + .switch-label .flag-spanish {
      opacity: 0;
  }
  
  input:checked + .switch-label .flag-english {
      opacity: 1;
  }
  </style>
  